@import url(https://fonts.googleapis.com/css2?family=Sulphur+Point:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: 'Sulphur Point', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(47, 61, 131);
  overflow-x: hidden;
  overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
    overscroll-behavior-y: contain;
}

.slider-navigation{
	margin-top: 30px;
	margin-left: 2.08vw;
	z-index: -10;
}

.slider-navigation li{
	list-style: none;
}

.slider-navigation li{
	display: table;
	cursor: pointer;
	font-size: 30px;
	font-weight: 800;
	margin-top: 10px;
	border-radius: 300px;
	border: 1px solid rgb(47, 61, 131);
}

.slider-navigation li span{
	text-align: center;
	display: table-cell;
	vertical-align: middle;
	padding: 0px 15px 15px 15px;
	line-height: 20px;
}

.slider-navigation li.active{
	border: 1px solid #FFFFFF;
}

.slider-navigation li:hover{
	background: #FFFFFF;
	color: rgb(47, 61, 131);
}

@media screen and (max-width: 768px){
	.slider-navigation{
		padding: 70vh 0 0;
		margin: 0 auto;
		width: 70vw;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid rgb(47, 61, 131);
  border-color: rgb(47, 61, 131) transparent rgb(47, 61, 131) transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.earth{
	position: fixed;
	top: 0px;
	z-index: -10;
}

@media screen and (max-width: 768px){

	.earth{
		position: absolute;
		top: -5vh;
	}
}
.home__content{
	z-index: 10;
	padding-top: 30vh;
	padding-bottom: 38vh;
	color: #FFFFFF;
	display: flex;
	flex-flow: row;
	overflow: hidden;
}

.home__left-section{
	width: 24vw;
	font-size: 4.44vw;
	padding-left: 10.41vw;
	line-height: 4.444vw;
}

.home__divider{
	width: 50vw;
}

.home__right-section{
	text-align: right;
	width: 31vw;
	font-size: 1.7vw;
	padding-top: 50px;
	padding-right: 5.55vw;
	line-height: 2.3vw;
}

.home__right-section button{
	margin-top: 30px;
	font-size: 0.972vw;
	border: 1px solid #FFFFFF;
	background: none;
	color: #FFFFFF;
	border-radius: 50px;
	padding: 13px 2.78vw 15px;
	text-transform: uppercase;
	transition: all 0.3s;
	cursor: pointer;
}

.home__earth-background{
	position: fixed;
	background: url(/static/media/pastel.b8b28094.jpg);
	background-size: cover;
	background-position: center;
	left: 50%;
	-webkit-transform: translate(-50%, 0%);
	        transform: translate(-50%, 0%);
	width: 30vw;
	height: 95vh;
	top: 2.5vh;
	z-index: -10;
}

.home__right-section button:hover{
	background: #FFFFFF;
	color: rgb(47, 61, 131);
}

@media screen and (max-width: 768px){
	.home__content{
		display: block;
		padding-top: 10px;
		padding-bottom: 5vh;
	}

	.home__left-section{
		font-size: 40px;
		line-height: 40px;
		width: auto;
		padding: 20px;
	}

	.home__right-section{
		font-size: 20px;
		line-height: 20px;
		width: auto;
		padding: 20px;
		text-align: center;
	}

	.home__right-section button{
		width: 100%;
		font-size: 14px;
	}

	.home__divider{
		display: none;
	}

	.home__earth-background{
		position: absolute;
		top: 15vh;
		width: 90vw;
		height: 55vh;
	}
}
.navbar{
	z-index: 20;
	position: fixed;
	top: 0px;
	left: 0px;
	display: flex;
	flex-flow: row;
	width: 100vw;
	height: 100px;
	padding: 15px 0px;
}

.navbar__logo-container{
	width: 50vw;
	display: flex;
	justify-content: flex-start;
	padding-left: 4.86vw;
}

.navbar__logo-container img{
	max-height: 60px;
	cursor: pointer;
}

.navbar__menu-container{
	width: 50vw;
	display: flex;
	justify-content: flex-end;
	padding-right: 4.86vw;
}

.navbar__menu-container svg{
	cursor: pointer;
}

